<template>
  <div class="index-container">
    <div :class="{'mdui-col-md-8':topicConfig.menu}" style="margin-bottom: 50px">
      <div class="mdui-card post-card">
        <div class="mdui-card-media post-card-media">
          <div class="index-img entered loaded"
               :data-bg="topic.cover"
               data-ll-status="loaded"
               :style="`background-image: url(${topic.cover});`"></div>
          <div class="mdui-card-media-covered">
            <div class="mdui-card-primary">
              <div class="mdui-card-primary-title">{{ topic.title }}</div>
              <div class="mdui-card-primary-subtitle">
                {{ topic.view }} 浏览 | {{ dayjs(topic.createTime).format(basicConfig.dateFormat) }} | 分类:
                <router-link :to="{name:'categories',params:{name:topic.categoryId}}" data-pjax-state="">
                  {{ category.name }}
                </router-link>
                <span v-show="tags.length>0">| 标签:</span>
                <span v-for="(item,index) in tags" :key="index">
                  #{{ item.name }}
                </span>
              </div>
            </div>
          </div>
        </div>
        <div style="min-height: 500px">
          <div id="preview" style="margin: 20px"></div>
        </div>
      </div>
    </div>
    <div v-show="topicConfig.menu"  class="mdui-col-md-4" id="menu" style="position: sticky;top: 0;margin-top: 0">
      <!--<Introduce></Introduce>-->
      <div v-show="menuShow" class="mdui-card mdui-hoverable sidebar-module show_menu">
        <div class="mdui-list">
          <div class="sidebar-module-title" id="title">目录</div>
          <div id="outline" style="display: block">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Introduce from "../components/Card/Introduce";
import {getTopicApi} from "../api/article";
import Vditor from "vditor";
import 'vditor/dist/index.css'
import {mapGetters} from "vuex";

export default {
  name: "Post",
  // components: {Introduce},
  data() {
    return {
      topic: {
        cover: '',
        categoryId: 0
      },
      category: {name: ''},
      tags: [],
      menuShow: false,
      fixed: false
    }
  },
  computed: {
    ...mapGetters(['basicConfig','topicConfig'])
  },
  created() {
    this.getDetail()
  },
  mounted() {
    // this.navH =this.$refs.outline.offsetTop;
    // window.addEventListener('scroll', this.handleScroll)
  },
  methods: {
    renderMarkdown(md) {
      Vditor.preview(document.getElementById('preview'), md, {
        hljs: {style: 'native'},
      })
    },
    async renderMenu(md) {
      const previewElement = document.getElementById('outline')
      let html = await Vditor.md2html(md)
      if (html === '') {
        return
      }
      this.menuShow = true
      previewElement.innerHTML = html
      Vditor.outlineRender(previewElement, previewElement)
      this.handleScroll()
    },
    getDetail() {
      getTopicApi(this.$route.params.id).then(res => {
        //console.log(res)
        let {topic, category, tags} = res.data
        this.topic = topic
        this.category = category
        this.tags = tags
        this.renderMarkdown(this.topic.content)
        if (this.topicConfig.menu){
          this.renderMenu(this.topic.content)
        }
        document.title = topic.title
      })
    },
    handleScroll() {
      const headingElements = []
      Array.from(document.getElementById('preview').children).forEach((item) => {
        if (item.tagName.length === 2 && item.tagName !== 'HR' && item.tagName.indexOf('H') === 0) {
          headingElements.push(item)
        }
      })

      let toc = []
      window.addEventListener('scroll', () => {
        const scrollTop = window.scrollY
        toc = []
        headingElements.forEach((item) => {
          toc.push({
            id: item.id,
            offsetTop: item.offsetTop,
          })
        })

        const currentElement = document.querySelector('.vditor-outline__item--current')
        for (let i = 0, iMax = toc.length; i < iMax; i++) {
          if (scrollTop < toc[i].offsetTop) {
            if (currentElement) {
              currentElement.classList.remove('vditor-outline__item--current')
            }
            let index = i > 0 ? i - 1 : 0
            let a = document.querySelector('span[data-target-id="' + toc[index].id + '"]')
            let b = document.querySelector('span[data-target-id="' + toc[index].id + '"] +ul')
            let c = document.querySelector('span[data-target-id="' + toc[index].id + '"] svg')
            if (b) {
              b.style.display = "block"
              c.classList.remove('vditor-outline__action--close')
            }
            a.classList.add('vditor-outline__item--current')
            if (a.offsetTop < 300) {
              document.querySelector("#outline").scrollTo(0,0)
            }else {
              document.querySelector("#outline").scrollTo(0,a.offsetTop-200)
            }
            break
          }
        }
      })
    }
  },
}
</script>

<style scoped>
#outline {
  width: 100%;
  max-height: 500px;
  overflow-y: auto;
}


/*#outline::-webkit-scrollbar {
  display: none; !* Chrome Safari *!
}*/
@media (max-width: 1024px) {
  #menu {
    display: none !important;
  }
}

.index-container {
  padding-top: 0 !important;
}
</style>
<style>
#outline li > span.vditor-outline__item--current {
  border-left: 1px solid #4285f4;
  color: #4285f4;
  background-color: #f6f8fa;
}

#outline li > span:hover {
  color: #4285f4;
  background-color: #f6f8fa;
}
</style>
